import React from "react";
import "./news.scss";
import Section from "../Section";
import * as moment from "moment";
import data from "../../api/data";
import { markedInline } from "../../utils/helpers";
import "./Awards.scss";

function formatDate(str, _default) {
  const date = moment(str, moment.ISO_8601, false);
  if (date.isValid()) {
    return date.format("YYYY");
  }
  return _default;
}

function Award(props) {
  const { award } = props;
 
  return (
    <tr>
      <td className="date">
        <span className="award-date">{formatDate(award.date, award.date)}</span>
      </td>
      <td className="content">
        <span className="award-name" dangerouslySetInnerHTML={{ __html: markedInline(award.name) }} />
        {award.content &&
          <React.Fragment>
            <br/>
            <p className="award-content" dangerouslySetInnerHTML={{ __html: markedInline(award.content)}}/>
          </React.Fragment>
        }
      </td>
    </tr>
  );
}

class _Awards extends React.Component {
  render() {
    const { title, awards } = this.props;
    return (
      <Section id="awards">
        <h2 dangerouslySetInnerHTML={{ __html: markedInline(title) }} />
        {awards && (
          <table className="awards">
            <tbody>
              {awards.map((item, i) => (
                <Award award={item} key={i}/>
              ))}
            </tbody>
          </table>
        )}
      </Section>
    );
  }
}

export default function Awards(props) {
  const awards = data.awards || [];
  const title = "Honors and Awards";
  return <_Awards title={title} awards={awards} {...props} />;
}
